/******************************
*
*  RoyalSlider Default Skin 
*
*    1. Arrows 
*    2. Bullets
*    3. Thumbnails
*    4. Tabs
*    5. Fullscreen button
*    6. Play/close video button
*    7. Preloader
*    8. Caption
*    
*  Sprite: 'rs-default.png'
*  Feel free to edit anything
*  If you don't some part - just delete it
* 
******************************/
/* Background */
.rsDefault,
.rsDefault .rsOverflow,
.rsDefault .rsSlide,
.rsDefault .rsVideoFrameHolder,
.rsDefault .rsThumbs {
  background: #000;
  color: #FFF; }

/***************
*
*  1. Arrows
*
****************/
.rsDefault .rsArrow {
  height: 100%;
  width: 44px;
  position: absolute;
  display: block;
  cursor: pointer;
  z-index: 21; }

.rsDefault.rsVer .rsArrow {
  width: 100%;
  height: 44px; }

.rsDefault.rsVer .rsArrowLeft {
  top: 0;
  left: 0; }

.rsDefault.rsVer .rsArrowRight {
  bottom: 0;
  left: 0; }

.rsDefault.rsHor .rsArrowLeft {
  left: 0;
  top: 0; }

.rsDefault.rsHor .rsArrowRight {
  right: 0;
  top: 0; }

.rsDefault .rsArrowIcn {
  width: 32px;
  height: 32px;
  top: 50%;
  left: 50%;
  margin-top: -16px;
  margin-left: -16px;
  position: absolute;
  cursor: pointer;
  color: #fff;
  font-size: 25px;
  float: none;
  display: inline-block;
  background-color: rgba(0, 0, 0, 0.75);
  *background-color: #000;
  border-radius: 2px; }
  .rsDefault .rsArrowIcn:after {
    content: '\e80b';
    position: absolute;
    line-height: 32px;
    font-family: bt-icons;
    width: 32px;
    height: 32px;
    text-align: center;
    color: #fff;
    display: inline-block; }

.rsArrowLeft .rsArrowIcn:after {
  content: '\e80a'; }

.rsDefault .rsArrowIcn:hover {
  background-color: rgba(0, 0, 0, 0.9); }

.rsDefault .rsArrowDisabled .rsArrowIcn {
  opacity: .2;
  filter: alpha(opacity=20);
  *display: none; }

/***************
*
*  2. Bullets
*
****************/
.rsDefault .rsBullets {
  height: 30px;
  z-index: 35;
  width: 100%;
  height: auto;
  margin: 0 auto;
  text-align: center;
  line-height: 1;
  overflow: hidden;
  margin-bottom: 10px; }

