  background: #fff; }

.rsDefault .rsBullet.rsNavSelected span {
  background-color: #cd0403; }

/***************
*
*  3. Thumbnails
*
****************/
.rsDefault .rsThumbsHor {
  width: 100%;
  height: 72px; }

.rsDefault .rsThumbsVer {
  width: 96px;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0; }

.rsDefault.rsWithThumbsHor .rsThumbsContainer {
